.calculator-body {
    height: 534px;
    width: 300px;
    border-radius: 4px;
    background: #172346;
    display: flex;
    flex-direction: column;
    -webkit-box-shadow: 0px 0px 29px 0px rgba(24,31,53,0.2);
    -moz-box-shadow: 0px 0px 29px 0px rgba(24,31,53,0.2);
    box-shadow: 0px 0px 29px 0px rgba(24,31,53,0.2);
}

.calculator-output {
    background: #333E5D;
    height: 160px;
    width: 100%;
    border-top-right-radius: 4px;
    border-top-left-radius: 4px;
    display: flex;
    flex-direction: column;
}

.calculator-output > .calculation {
    padding: 20px;
    display: flex;
    flex-direction: row;
    justify-content: flex-end;
    min-height: 17px;
}

.calculator-output > .calculation > span {
    font-family: 'Roboto Mono' !important;
    color: #ABAFBC;
    font-size: 12px;
}

.calculator-output > .calculation-result {
    padding: 20px;
    display: flex;
    flex-direction: row;
    justify-content: flex-end;
}

.calculator-output > .calculation-result > h3 {
    font-size: 45px;
    color: #fff;
    font-family: 'Roboto Mono' !important;
    margin: 0 !important;
}
